.popup-slide-1, .popup-slide-2, .popup-slide-3{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left:0;
    z-index: 1022;
    background-color: rgba(0, 0, 0, 0.705);
    visibility: hidden;
    transform: translateX(2000px);
    transition: all .2s;
    transition-timing-function: cubic-bezier(.28,.19,.73,.84);
}

.popup-slide-2{
    transition-delay: .4s;
}

.menu{
    width: 49.3rem;
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    background-color: var(--color-drak-blue);
    display: block;

    &__list{
        padding: 5rem 4rem;
    }

    &__list-normal{
        li{
            color: var(--color-white);
            font-size: 2rem;
            margin-top: 2rem;
            display: block;
            cursor: pointer;
            &:first-child{
                margin-top: 1rem;
            }
        }
        li > a{
            color: var(--color-white);
            font-size: 2rem;
            display: block;
        }
    }

    &__header{
        min-height: 24.6rem;
        display: flex;
        &__start{
            width: 38.6rem;
            background-color: var(--color-orange);
            color: var(--color-white);
            align-self: stretch;
            border-radius: 0 0 12.5rem 0;
            padding: 7rem 4rem;
            display: flex;
            align-items: center;
        
           
            .user-name{
                font-size: 2.2rem;
                font-family: var(--font-regular);
                display: block;
            }
            .login-btn{
                cursor: pointer;
                display: inline-block;
                
            }
        }

        &__end{
            display: flex;
            justify-content:space-between;
            flex-direction: column;
            align-items: center;
        }
    }
}

.user-image{
    width: 9rem;
    height: 9rem;
    border-radius:50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-inline-end: 2.2rem;
    border: 2px solid var(--color-light-blue);

    .icon-guest{
        width: 4.8rem;
        height: 4.8rem;
        
    }
    .user-img{
        @extend%object-fit;
        object-position: top center;
        display: none;
    }
}

#login{
    display: none;
}

.login{
    width: 40rem;
    padding: 4.4rem 3rem;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 3rem;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: var(--color-white);
    text-align: center;
    

    input{
        height: 5rem;
        width: 100%;
        border: none;
        border-radius: 3rem;
        background-color: rgba(255, 255, 255, 0.301);
        padding: 1.6rem 2rem;
        outline: none;
        color: var(--color-white);
        font-family: inherit;
        ::placeholder{
            font-family: var(--font-light);
            font-size: 1.6rem;
            opacity: .4;
            color: var(--color-white);
        }
        &:first-child{
            margin-bottom: 1.4rem;
        }
    }

    .forget-psword{
        color: rgba(255, 255, 255, 0.80);
        display: block;
        text-align: right;
        margin: 10px 0;
        font-size: 1.4rem;
    }

    .login-btn{
        height: 5.4rem;
        width: 100%;
        border: none;
        background-color: var(--color-orange);
        font-family: var(--font-medium);
        display: block;
        border-radius: 3rem;
        margin: 2rem 0;
        font-size: 1.6rem;
        letter-spacing: 3px;
        color: var(--color-white);
        cursor: pointer;
    }

    .btn-guest{
        display: block;
        font-size: 1.8rem;
        font-family: var(--font-medium);
        color: var(--color-white);
    }

    .social-login{
        margin: 3rem 0;
        span{
            display: block;
            margin-bottom: 1.7rem;
        }

        .icon{
            display: inline-block;
            width: 4rem;
            height: 4rem;
            margin: 0 6px;
        }
    }

    .sign-up-text{
        opacity: .4;
        font-size: 1.6rem;
       
    }
    .sign-up-btn{
        font-family: font(--font-medium);
        font-size: 1.6rem;
        display: inline-block;
        margin-inline-start: 6px;
        cursor: pointer;
    }
    .download-app{
        position: relative;
       margin-top: 4.4rem;
        span{
            display: block;
            margin-bottom: 1rem;
            letter-spacing: 1.5px;
        }

        &::before{
            content: "";
            display: inline-block;
            top: -2rem;
            left: -3rem;
            position: absolute;
            z-index: 1;
            width: calc(100% + 6rem);
            height: 1px;
            background-color: #707070;
        }
    }
}


#createAccount{
    display: none;
}
.create-account{
    input{
        &::placeholder{
            color: rgba(255, 255, 255, 0.4)
        }
        &:not(:last-child){
            margin-bottom: 10px;
        }
}

.back-btn{
        color: rgba(255, 255, 255, 0.5);
        display: flex;
        align-items: center;
        text-align: center;
       justify-content: center;
        .icon{
            width: 7.5px;
            height: 15px;
            stroke: rgba(255, 255, 255, 0.5);
           margin-inline-end: 5px;
        }
    }
}

.popup-slide-2, .popup-slide-3{
    .menu{
        padding: 5rem 4.4rem;
    }
}
.setting-popup{
    overflow-x: auto;
    
    &__head{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__start{
        span{
            display: inline-block;
            color: var(--color-orange);
            text-transform: uppercase;
            font-size: 2.4rem;
            font-family: var(--font-regular);
        }
    }

    &__end{
        .close-btn{
            padding: 0 !important;
           &::after {
                content: "";
                background-image: url(../img/close-btn.svg);
                width: 1.7rem;
                height: 1.7rem;
                background-size: contain;
                background-position: center;
                display: inline-block;
            }
        }
    }

    .btn-settings{
        display: inline-block;
        background-color: var(--color-orange);
        color: var(--color-white);
        font-size: 13px;
        font-family: var(--font-bold);
        letter-spacing: 3px;
        text-transform: uppercase;
        width: 100%;
        border-radius: 2.1rem;
        padding: 1.2rem 2rem;
        border: none;
        margin-top: 5rem;
        height: 4.2rem;
        cursor: pointer;
    }
}

.setting-group{
    margin-top: 5rem;
    color: var(--color-white);
    &__item{
        
    }

    &__wrap{
        
    }

    &__radio-item{
        display: inline-flex;
        &:not(:last-child){
            margin-inline-end: 1.2rem;
        }
    }

    &__title{
        font-size: 1.6rem;
        font-family: var(--font-regular);
        display: block;
        margin-bottom: 12px;

    }

    &__radio-custom{
        background-color: transparent;
        color: var(--color-light-blue);
        padding: 0 1.4rem;
        min-width: 8rem;
        height: 3.5rem;
        border-radius: 2rem;
        text-transform: uppercase;
        font-size: 12px;
        border: 1px solid var(--color-light-blue);
        cursor: pointer;
        display: inline-block;
        text-align: center;
        font-family: var(--font-book);
        width: max-content;
        height: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
       
    }

    &__radio-wrap{
        display: inline-flex;
        align-items: center;
    }

    &__input{
        display: none;
    }

    &__input:checked + &__label &__radio-custom{
        background-color: var(--color-light-blue);
        color: var(--color-white);
    }



    &__label{
        display: inline-block;
    }

    
}

.radio-scroll{
    .slick-slide{
        margin: 0 1rem;
    }
.setting-group__radio-item{
    margin: 0;
}

}
